@import url(https://fonts.googleapis.com/css2?family=Nanum+Gothic:wght@400;700&family=Noto+Sans+KR:wght@350;400;500&family=Roboto:wght@500&display=swap);
@charset "utf-8";

* {box-sizing:border-box}

html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video { margin:0; padding:0; border:0; outline:0; vertical-align:top; background:transparent; }

body { line-height:1; }

article,aside,details,figcaption,figure, footer,header,hgroup,menu,nav,section { display:block; }

ul,ol { list-style:none; }

blockquote, q { quotes:none; }

blockquote:before, blockquote:after,
q:before, q:after { content:''; content:none; }

a {
    margin:0; padding:0; font:inherit; background:transparent; text-decoration: none; color:inherit }


button {border: none; background: none; outline:0; padding:0;}
input:focus {outline:0;}
table { width:100%; border-collapse:collapse; border-spacing:0; }
h1,h2,h3,h4,h5,h6 {font-weight: normal;}
em, i, strong, b {font-weight: inherit; font-style:normal}
img {max-width:100%; }

/* change colours to suit your needs */
mark { background-color:#ff9; color:#000; font-style:italic; font-weight:bold; }
hr {
    display:block;
    height:1px;
    border:0;
    padding:0;
}

html {font-size:62.5%}

.blind_block {
    position : absolute; width : 1px; height : 1px;
	padding : 0; margin : -1px; overflow : hidden;
	border : 0; 
	clip: rect(1px, 1px, 1px, 1px);
  -webkit-clip-path: inset(50%);
          clip-path: inset(50%);
}

.blind_inline {
    overflow: hidden;
    display: inline-block;
      width: 0; height: 0;
      margin: -1px;
      border:0;  
      clip: rect(0, 0, 0, 0);
    -webkit-clip-path: inset(50%);
            clip-path: inset(50%);
}

